import React, { Component } from 'react'

export default class bookcase extends Component {
    constructor(props){
        super(props)
        
    }
    render() {
        return (
            <div className="bbox">
                <div className="nav">书架</div>
                <div className="menu">
                    <div className="menuitem">分类</div>
                </div>
                <div className="menulist">
                    <div className="listitem" >
                        <div className="boxL">
                            <div className="Lname">名字</div>
                            <div className="Lprice">联系方式</div>
                        </div>
                        <div className="boxR">价格</div>
                    </div>
                </div>
            </div>
        )
    }
}
